import React from "react";
import Header from './Header';

import {
    TextField,
    Link,
    Container
} from "@material-ui/core";

import { signup } from "./ApiService";

class SignUpPage extends React.Component{
    constructor(props) {
        super(props);
        this.handleSubmit = this.handleSubmit.bind(this);
    }

    handleSubmit(event) {
        event.preventDefault();
        const data = new FormData(event.target);
        const username = data.get("username");
        const email = data.get("email");
        const password = data.get("password");
        const phoneNumber = data.get("phoneNumber");
        const birth = data.get("birth");
        signup({username:username,email:email,password:password,phoneNumber:phoneNumber,birth:birth}).then(
            (response) => {
                window.location.href="/";
            }
        );
    }

    render() {
        return(
            <><Header /><Container component="main" maxWidth="xs">
                <div className="main-title"> </div>
                <br/>
                <form noValidate onSubmit={this.handleSubmit}>
                    <div container spacing={2}>
                        <div item xs={12}>
                            <TextField
                                autoComplete="fname"
                                name="username"
                                variant="outlined"
                                required
                                fullWidth
                                id="username"
                                label=" "
                                autoFocus />
                        </div>
                        <h6> </h6>
                        <div item xs={12}>
                            <TextField
                                variant="outlined"
                                required
                                fullWidth
                                id="email"
                                label=" "
                                name="email"
                                autoComplete="email" />
                        </div>
                        <h6> </h6>
                        <div item xs={12}>
                            <TextField
                                variant="outlined"
                                required
                                fullWidth
                                name="password"
                                label=""
                                type="password"
                                id="password"
                                autoFocus="current-password" />
                        </div>
                        <h6> </h6>
                        <div item xs={12}>
                            <TextField
                                variant="outlined"
                                required
                                fullWidth
                                name="phoneNumber"
                                label=""
                                type="phoneNumber"
                                id="phoneNumber" 
                                oninput="PhoneLength(this)"
                                maxlength="11"/>
                        </div>
                        <h6> </h6>
                        <div item xs={12}>
                            <TextField
                                variant="outlined"
                                required
                                fullWidth
                                name="birth"
                                label=""
                                type="birth"
                                id="birth" />
                        </div>
                        <div item xs={12} className="setcenter">
                            <button
                                className="button"
                                type="submit"
                                style={{marginBottom:"30px"}}
                            >
                                 
                            </button>
                        </div>
                    </div>
                    <div className="setcenter">
                        <Link href="/signuppage" varient="body2" style={{ textDecoration: "none", color: "black"}}>
                            <div className="setcenter" style={{textDecoration:"underline"}}>  ?  .</div>
                        </Link>
                    </div>
                </form>
            </Container></>
        );
    }
}

export default SignUpPage;