import React from "react";
import { Link, useNavigate } from "react-router-dom";
import styled from "styled-components";
import City from "../City";
import { citydummy } from "../../citydummy";
import Header from './Header';

function KyotoInformationPage(){
    return (
        <div>
          <Header/>
          <div className="main-title"></div>
          <div className="city-explanation-container" style={{display: 'flex'}}>
                <img src="travel_img/kyoto.jpg" alt=""/>
                <div style={{whiteSpace:'pre-wrap'}}>    ,         .  3            .                  .
                      ,     .                    .     .            .
                </div>
          </div>
          <div className="main-title"> </div>
          <div className="app-container">
            {
              citydummy.map((item)=>{
                return(
                  <City
                    destination={item.destination}
                    travel_pic={item.travel_pic}
                  />
                )
              })
            }
          </div>
          <Link to="/surveypage" style={{textDecoration:"none", color:"black"}}><div className="city-decision">  </div></Link>
        </div>
      );
}

export default KyotoInformationPage;