import React from "react";
import { Link } from "react-router-dom";
import City from "../City";
import { citydummy } from "../../citydummy";
import Header from './Header';

function SapporoInformationPage(){
    return (
        <div>
          <Header/>
          <div className="main-title"></div>
          <div className="city-explanation-container" style={{display: 'flex'}}>
                <img src="travel_img/sapporo.jpg" alt=""/>
                <div style={{whiteSpace:'pre-wrap'}}>      . 3     ,  4 ~5    .      . 7     , 8      . 9      . 10    10    . 11    . 12       . 12 2              .
                </div>
          </div>
          <div className="main-title"> </div>
          <div className="app-container">
            {
              citydummy.map((item)=>{
                return(
                  <City
                    destination={item.destination}
                    travel_pic={item.travel_pic}
                  />
                )
              })
            }
          </div>
          <Link to="/surveypage" style={{textDecoration:"none", color:"black"}}><div className="city-decision">  </div></Link>
        </div>
      );
}

export default SapporoInformationPage;