import React, { useState } from "react";
import { UserEmail } from "./ApiService";
import { city } from "./Export";
import { Button } from "react-bootstrap";

function ReadMoreLess({name,address,localAddress,phoneNumber,latitude,longitude,
  sun,mon,tue,wed,thu,fri,sat,keywordImgUrl,id,arrive_time,depart_time,move_time}) {
  const [isShowMore, setIsShowMore] = useState(false);

  const [emailName, setEmailName] = useState({
    email: localStorage.getItem(UserEmail),
    name: name,
    city: city[0]
  });

  const toggleReadMoreLess = () => {
    setIsShowMore(!isShowMore);
    fetch('http://letstrip.shop:5000/to_update', {  // <-- Change the URL here
        method: 'POST',
        headers: {
          'Content-Type': 'application/json'
        },
          body: JSON.stringify(emailName)
      })
      .then(response => response.json())
  };

  return (
    <div>
      {isShowMore && (
        <div style={{}}>
          <div style={{display:"flex", padding:"0px 20px"}} className="setcenter">
          <div style={{fontSize:"26px", width:"700px"}}>
            <div>
              {name===" "||name===" "?
                null
              :
              <div className="result-info" style={{paddingRight:'20px'}}>
                <img src={keywordImgUrl} style={{height:"350px"}} alt=""/>
              </div>
              }
            </div>
            <br/>
              <div style={{marginRight:"20px", textAlign:"left", fontFamily:"Arial, sans-serif"}}>
              <br/>
                <div style={{fontWeight: "bold", paddingBottom: "10px"}}></div> <div>: {address}</div>
                <br/>
                <div style={{fontWeight: "bold", paddingBottom: "10px"}}></div><div> : {localAddress}</div>
                <br/>
                <div style={{fontWeight: "bold", paddingBottom: "10px"}}></div><div> : {phoneNumber}</div>
              </div>
              <br/>
              <div style={{marginRight:"20px", textAlign:"left", fontFamily:"Arial, sans-serif"}}>
                <div style={{fontWeight: "bold", paddingBottom: "10px"}}> </div><div> : {arrive_time}</div>
                {depart_time!==null?
                  <div>
                    <br/>
                    <div style={{fontWeight: "bold", paddingBottom: "10px"}}> </div> : {depart_time}
                  </div>
                  :
                  <div>
                    <br/>
                    <div style={{fontWeight: "bold", paddingBottom: "10px"}}> </div> : (.)
                  </div>
                }
                <br/>
                <div style={{fontWeight: "bold", paddingBottom: "10px"}}> </div><div> : {move_time}</div>
                </div>
          </div>
              <div style={{fontSize: "40px", padding:"25px",fontFamily:"Arial, sans-serif", border: "1px solid grey",borderRadius:"5px"}}>
                <div style={{fontWeight:"bold"}}></div>
                <br/>
                <div> : {sun}</div>
                <br/>
                <div> : {mon}</div>
                <br/>
                <div> : {tue}</div>
                <br/>
                <div> : {wed}</div>
                <br/>
                <div> : {thu}</div>
                <br/>
                <div> : {fri}</div>
                <br/>
                <div> : {sat}</div>
              </div>
          </div>
          <br/>
        </div>
      )}
      <div className="setcenter" style={{marginTop:"-30px"}}>
        <Button className="button" onClick={toggleReadMoreLess}>
          {isShowMore ? "" : ""}
        </Button>
      </div>
    </div>
  );
}

export default ReadMoreLess;