import React from "react";
import { Link } from "react-router-dom";
import City from "../City";
import Header from './Header';
import { OsakaCitydummy } from "../../OsakaCitydummy";

function OsakaInformationPage(){
    return (
        <div>
          <Header/>
          <div className="main-title"></div>
          <br/>
          <div className="city-explanation-container" style={{display: 'flex'}}>
            <img src="travel_img/osaka.jpg" alt="" style={{borderRadius:"5px"}}/>
            <div style={{whiteSpace:'pre-wrap', borderRadius:"5px", boxShadow:"3px 3px 5px rgba(0, 0, 0, 0.1)"}}>     .          ,     .
            4      1600              23    2,     3. 1956    .      ,     .
            </div>
          </div>
          <div className="main-title" style={{backgroundColor:"#f5f5f7"}}> </div>
          <div className="app-container">
            {
              OsakaCitydummy.map((item)=>{
                return(
                  <City
                    destination={item.destination}
                    travel_pic={item.travel_pic}
                  />
                )
              })
            }
          </div>
          <Link to="/surveypage" style={{textDecoration:"none", color:"black"}}><div className="city-decision">  </div></Link>
        </div>
      );
}

export default OsakaInformationPage;