import React from "react";
import { Link } from "react-router-dom";
import City from "../City";
import Header from './Header';
import { TokyoCitydummy } from "../../TokyoCitydummy";

function TokyoInformationPage(){
    return (
        <div>
          <Header/>
          <div className="main-title"></div>
          <br/>
          <div className="city-explanation-container" style={{display: 'flex'}}>
            <img src="travel_img/tokyo.jpg" alt="" style={{borderRadius:"5px"}}/>
            <div style={{whiteSpace:'pre-wrap'}}>  .         . 
                   1,400 ,      23    970 .      ,      2020   4,350  .           ,        .   ,    3   .
                 , 11     .           ,          .     . 1868         .
            </div>
          </div>
          <div className="main-title" style={{backgroundColor:"#f5f5f7"}}> </div>
          <div className="app-container">
            {
              TokyoCitydummy.map((item)=>{
                return(
                  <City
                    destination={item.destination}
                    travel_pic={item.travel_pic}
                  />
                )
              })
            }
          </div>
          <Link to="/surveypagetokyo" style={{textDecoration:"none", color:"black", borderRadius:"5px", boxShadow:"3px 3px 5px rgba(0, 0, 0, 0.1)"}}><div className="city-decision">  </div></Link>
        </div>
      );
}

export default TokyoInformationPage;